import React from 'react';
import { Good } from './types/Good';

type Props = {
  goods: Good[],
  isError: boolean
};

export const GoodsList: React.FC<Props> = ({ goods, isError }) => (
  <>
    { isError
      ? (<p>Error, server is unavailable</p>)
      : (
        <ul>
          {goods.map(good => (
            <li
              key={good.id}
              data-cy="good"
              style={{ color: good.color }}
            >
              {good.name}
            </li>
          ))}
        </ul>
      )}
  </>
);
